import React,{useEffect} from 'react';
import {connect} from "react-redux";
import {updateAppSettings, updateDataStore} from "src/actions/foundationActions";
//import CheckMark from 'src/components/icons/CheckMark'
import {createConnectionTimer, loadConnectionConsts} from "src/containers/foundation/foundationFunctions";
import store from 'src/store';
import {loadNewMod} from "src/utilities/utilityFunctions";

const Connection = () => {
    let stateData = store.getState();
    useEffect(()=>{
        createConnectionTimer();
        loadConnectionConsts();
        //eslint-disable-next-line
    },[]);

    useEffect(()=>{
        if(stateData.appSettings.connectionActiveModName){
            loadNewMod(stateData.appSettings.connectionActiveModName,(module)=>store.dispatch(updateDataStore({connectionActiveModule:module.default})));
        }
    },[stateData.appSettings.connectionActiveModName])

    useEffect(()=>{
        if(stateData.appSettings.connectionInActiveModName){
            loadNewMod(stateData.appSettings.connectionInActiveModName,(module)=>store.dispatch(updateDataStore({connectionInActiveModule:module.default})));
        }
    },[stateData.appSettings.connectionInActiveModName])
    let activeMod = stateData.dataStore.connectionActiveModule ? <stateData.dataStore.connectionActiveModule /> : null;
    let inActiveMod = stateData.dataStore.connectionInActiveModule ? <stateData.dataStore.connectionInActiveModule /> : null;
    //console.dir(activeMod);
    let texter = stateData.appSettings.connectionLive ? activeMod : inActiveMod;
    return <div id="connectionWrapper" title="Connection to Server Status" style={{transform:"translateY(3px)",display:"inline-block"}}>{texter}</div>
};


const mapStateToProps = (state) =>{
    return {
        appSettings:state.appSettings
    };
};

export default connect(mapStateToProps,{
    updateAppSettings:updateAppSettings
})(Connection);
